* {
  font-family: "Montserrat", sans-serif;
}

.App {
  text-align: center;
}

#body {
  font-size: 20px;
}

 /* Split the screen in half */
 .split {
  height: 100%;
  width: 50%;
  position: fixed;
  top: 0;
  overflow-x: hidden;
}

/* Control the left side */
.left {
  left: 0;
  background-color: #1ab7bc;
}

/* Control the right side */
.right {
  right: 0;
  background-color: #fff;
}

/* If you want the content centered horizontally and vertically */

.center {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.centerbot {
  position: fixed;
  bottom: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bottom {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bigbutton {
  display: block;
  width: 100%;
  padding: 24px 38px;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
}

.bigtext {
  font-size: 35px;
}

.bigbutton {
  font-size: 30px !important;
}

.flatbutton {
  background: linear-gradient(to bottom, #0d6366, #2d8386);
  box-shadow: -1px 2px 3px 1px rgba(0, 0, 0, 0), -1px 2px 3px rgba(0, 0, 0, 0.3);
  outline:none;
  border: #0d6366 1px solid;
  color: white;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  border-radius: 12px;
  cursor:pointer;
}

.flatbutton:hover {
  background: linear-gradient(to bottom, #1d7376, #3d9396);
  outline:none;
}

.flatbutton:disabled {
  background: linear-gradient(to bottom, #cccccc, #aaaaaa);
  box-shadow: -1px 1px 3px 1px rgba(0, 0, 0, 0), -1px 1px 3px rgba(0, 0, 0, 0.3);
  outline:none;
  border: #888888 1px solid;
  opacity: 1.0; 
  cursor: not-allowed;
}